@use "../../mixin.scss" as mixin;

.detail-div {
	@include mixin.width-height($w: 100%, $h: auto);
	display: flex;
	justify-content: flex-start;
	align-items: flex-start;
	padding: 10px;
	gap: 20px;
	background-color: var(--secondary-bg);

	background-color: var(--primary-bg);

	.img-div {
		@include mixin.width-height($w: 300px);
		min-width: 300px;

		img {
			@include mixin.width-height();
			object-fit: cover;
			border-radius: 10px;
		}
	}

	.info-div {
		@include mixin.width-height();
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		flex-direction: column;

		h2 {
			font-family: var(--font-family);
			font-size: 35px;
		}

		.genres {
			@include mixin.width-height();
			display: flex;
			justify-content: flex-start;
			gap: 5px;
			flex-wrap: wrap;
			margin-top: 5px;

			span {
				@include mixin.width-height($w: auto);
				padding: 3px;
				padding-inline: 10px;
				border-radius: 5px;
				background-color: #eb1c3f;
				color: white;
				font-size: 13px;
				font-weight: bold;
				font-family: var(--font-family);
			}
		}

		.divider {
			@include mixin.width-height($h: 2px);
			margin: 10px auto;
			border-radius: 5px;
			background-color: #52726f;
		}

		.summary {
			color: var(--primary-fg);
			font-size: 18px;
			font-family: var(--font-family);
		}
	}
}
