@use "../../mixin.scss" as mixin;

.card-div {
	@include mixin.width-height($w: 250px, $h: auto);
	border-radius: 10px;
	box-shadow: 0px 0px 10px #0000003b;
	background-color: white;
	padding: 10px;

	cursor: pointer;

	.img-div {
		@include mixin.width-height($h: auto);
		border-radius: 10px;

		img {
			@include mixin.width-height($w: 100%, $h: auto);
			object-fit: cover;
			border-radius: 8px;
		}
	}

	.name-div {
        margin-top:10px;
		@include mixin.width-height();
		font-size: 20px;
		font-weight: bold;
		font-family: var(--font-family);
	}

    .genres {
        @include mixin.width-height();
        display: flex;
        justify-content: flex-start;
        gap: 10px;
        flex-wrap: wrap;
        margin-top: 5px;


        span {
            @include mixin.width-height($w: auto);
            padding-inline: 10px;
            border-radius: 5px;
            background-color: #eb1c3f;
            color: white;
            font-size: 13px;
            font-weight: bold;
            font-family: var(--font-family);
        }
    }
}
